import { getAllCourseData } from "~/api/course"
import CourseCard from '@/components/Common/CourseCard'
import Selection from '@/components/Common/Selection'
import { backScrollTop } from 'ais-common-utils'
import { couseType } from '@/config/mock'
export default {
  components: {
    CourseCard,
    Selection
  },
  async asyncData(context) {
    const params = { page: 1, }
    if (context.route.query.search) params.name = context.route.query.search
    const { data }  = await getAllCourseData(params)
    if(data.code ===0){
      return { list: data.data.entries, total: +(data.data.total) }
    }
  },
  data () {
    return {
      list: [],
      loading: false,
      finished: false,
      pagination: {
        page: 1
      },
      active: { 
      },
      navData: [
        {
          allKey:'全部课程',
          value: [4, 5, 2, 3, 7, 1].map(item => ({ title: couseType[item], value: item })),
          type:'categoryId'
        },
        { allKey:'全部类型', value:[ {title:'免费', value:1}, {title:"付费", value:2} ], type:'type' },
        { allKey: '课程难度', value:[ {title:'初级', value:1}, {title:"中级", value:2}, {title:"高级", value:3} ], type:'difficulty' }
      ]
    }
  },
  created () {
    if (this.list.length >= this.total) {
      this.finished = true
    }
    let { categoryId } = this.$route.query
    if(categoryId){
      this.active = {categoryId:Number(categoryId)}
      this.getListHand()
    }
  },
  methods: {
    async getListHand () {
      let params = {
        ...this.pagination,
        ...this.active
      }
      if (this.$route.query.search) params.name = this.$route.query.search
      const { data }  = await getAllCourseData(params)
      this.loading = false
      if (data.code === 0) {
        this.list = params.page === 1 ? data.data.entries : this.list.concat(data.data.entries)
        this.pagination.page = this.pagination.page + 1
        if (this.list.length === +(data.data.total)) {
          this.finished = true
          this.loading = false
        }
      }
    },
    search() {
      this.pagination.page = 1
      this.finished = false
      backScrollTop(0)
      this.getListHand()
    },
    onLoad(){
      setTimeout(() =>{
        this.getListHand()
      }, 500)
    }
  },
  head () {
    return {
      title: 'SCI论文写作公开课_国际学术会议交流直播-艾思云课堂',
      meta: [
        { hid: 'description', name: 'description', content: '艾思云课堂每天更新学术会议直播课程，学术交流点播课，sci论文公开课，促进学术交流，帮助科研人员全方位提升科研能力。' },
        { hid: 'keywords', name: 'keywords', content: '学术公开课，SCI写作课，学术交流课' }
      ]
    }
  }
}
